import React from 'react'

// Import typefaces
import 'typeface-montserrat'
import 'typeface-merriweather'

import profilePic from '../images/profile-pic.jpg'
import { rhythm } from '../utils/typography'

class Bio extends React.Component {
  render() {
    // console.log('in bio, props are: ', this.props)
    return (
      <div className="bio typist-hidden"
        style={{
          display: 'flex',
          //marginBottom: rhythm(2.5),
        }}
      >
        <img
          src={profilePic}
          alt={`Josh Schneider`}
          style={{
            marginRight: rhythm(1 / 2),
            marginBottom: 0,
            width: rhythm(2),
            height: rhythm(2),
            borderRadius: rhythm(2)
          }}
        />
        <div className="bio__content" dangerouslySetInnerHTML={{ __html: this.props.bio }}></div>
      </div>
    )
  }
}

export default Bio
